.quil {
  width: fit-content;
  margin: 2rem auto;
  border: 1px solid black;
  padding: 1rem;
  display: flex;
  align-items: center;
  gap: 80px;
}

select {
  outline: none;
  font-size: 25px;
  padding: 10px;
  background-color: dodgerblue;
  color: white;
  border-radius: 10px;
}

option {
  color: black;
  background: white;
}

i {
  cursor: pointer;
  padding: 10px;
  transform: scale(1.7);
}

.highlight {
  display: inline-block;
  height: 30px;
  width: 30px;
  border-radius: 10px;
  border: 1px solid black;
  background-color: #ffff00;
  opacity: 0.5;
  box-sizing: border-box;
}
