body {
  margin: 0;
}

.doc {
  margin: 2rem auto;
  box-shadow: 0 0 3px 1px rgba(0, 0, 0, 0.3);
  height: 1200px;
  width: 900px;
  padding: 4rem;
  box-sizing: border-box;
}

span {
  display: inline-block;
}

.cursoredText {
  animation: cursor;
  animation-iteration-count: infinite;
  animation-duration: 0.4s;
  animation-direction: alternate;
  box-sizing: border-box;
}

p {
  margin: 5px 0;
  cursor: text;
}

p.ul {
  padding-left: 2rem;
  position: relative;
}

p.ul::before {
  position: absolute;
  content: "";
  height: 10px;
  width: 10px;
  border-radius: 50%;
  background-color: black;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.bi-plus-lg {
  font-size: 40px;
}


@keyframes cursor {
  from {
    border-right: 1.5px solid rgba(0, 0, 0, 1);
  }
  to {
    border-right: 1.5px solid rgba(0, 0, 0, 0);
  }
}
